import React from 'react'
import { connect } from 'react-redux'
import {useSelector,useDispatch} from 'react-redux'
import { firestoreConnect } from 'react-redux-firebase'
import { compose } from 'redux'
import { Redirect } from 'react-router-dom'
import moment from 'moment'

function ProjectDetails(props){

//    console.log(props)

    const { project , auth }= props

    if(!auth.uid) return <Redirect to='/SignIn'/>

    return(
        <div style={{marginTop:'100px'}}>
            <div>
                {/* <span>{project.id}</span> */}
                <h3 style={{backgroundColor:'lightgreen'}}>{project.title}</h3>
                <p>{project.content}</p>
                <h6>{project.authorFirstName} {project.authorLastName}</h6>
                {/* <h4>{project.email}</h4> */}
                <h6>{moment(project.date.toDate()).calendar()}</h6>
                <h6>Status : {project.status ? project.status : 'Not Started' }</h6>
            </div>

        </div>
    )
}

const mapStateToProps=(state , myProps)=>{
    const id=myProps.match.params.id
    const projects = state.firestore.data.projects
    const project =projects ? projects[id]: null

   return{
       project : project,
       auth : state.firebase.auth
   }
}

export default compose(
    connect(mapStateToProps),
    firestoreConnect([{
        collection : 'projects'
    }])
) ( ProjectDetails);