import {NavLink} from 'react-router-dom'


const SignedOutLinks=()=>{
    return(
    
          <ul className='right'>
           <li><NavLink style={{backgroundColor:'grey'}}  to='/SignUp'>SignUp</NavLink></li>
           <li><NavLink  style={{backgroundColor:'grey'}} to='/SignIn'>Login</NavLink></li>
          </ul>
    )
    
       
}

export default SignedOutLinks