const initState={
    projects:[
        {
            id:1 , title : 'Farmers Friend' ,content : 'This is to help Farmers' ,date : 'jan 31st'
        },
        {
            id:2 , title : 'My School' ,content : 'This is to solve problems of schools' ,date : 'Feb 14th'
        },
        {
            id:3 , title : 'My Village' ,content : 'This is to develop our own Villages' , date : 'Jul 2nd'
        }
    ]
}

const projectReducer=(state=initState,action)=>{


    switch (action.type) {
        case 'CREATE_PROJECT':
            console.log('in Project Reducer..')
            console.log(action.project)
            return state;
        case 'CREATE_PROJECT_ERROR' : 
            console.log('error '+action.errorInfo)
            return state;
    
        default:
            return state;
    }

   
}

export default projectReducer