import React from 'react'

function Home(){
    return(
        <div style={{marginTop:'50px'}}>
        <div style={{display:'flex',flexDirection:'column'}}>
            <div>
                 <span style={{fontSize:'30px',fontFamily:'sans-serif'}}>Silicon Valley of India</span>
            </div>
            
           <div style={{backgroundColor:'powderblue',textAlign:'center'}}>
            <p style={{fontFamily:'cursive'}}>You can submit your Idea ,
                I will give top 3 Ideas to Students to implement<br/>
                <b>if it is successful</b>  they will start a company <br/>
               <b>even if they fail</b> they will get a good job 
             </p>
             </div>
            
             <footer style={{fontSize:'20px',fontFamily:'fantasy'}}>
             We will give good gifts for top 10 Ideas
             </footer>
        
        </div>
        </div>
    )

}

export default Home