import {NavLink} from 'react-router-dom'
import { connect } from 'react-redux'
import { signOut } from '../../store/actions/authActions'


const SignedInLinks=(props)=>{
    return(
       <ul className='right' style={{backgroundColor:'black'}}>
           <li><NavLink style={{backgroundColor:'grey'}}  to='/Dashboard'>Dashboard</NavLink></li>
           <li><NavLink  style={{backgroundColor:'grey'}} to='/CreateProject'>Submit Idea</NavLink></li>
           <li><a style={{backgroundColor:'grey'}} onClick={props.signOut}>LogOut</a></li>
           <li><NavLink style={{backgroundColor:'grey'}} to='/' className='btn btn-floating pink lighten-1'>{props.profile.initials}</NavLink></li>
       </ul>
    )
}

const mapDispatchToProps=(dispatch)=>{
    return {
        signOut : ()=>dispatch(signOut())        
         
    }
}

export default connect(null, mapDispatchToProps )(SignedInLinks) 