const initState={
   authError : ''
}

const authReducer=(state=initState,action)=>{
  
   switch (action.type) {
      case 'LOGIN_SUCCESS':
         console.log('Login successd')
         return {
            ...state ,
            authError : null
         }
      case 'LOGIN_ERROR' : 
      console.log('Login failed')
         return {
            ...state ,
            authError : 'Login Failed , please enter valid email & password'
         }

      case 'SIGHOUT_SUCCESS' : 
      console.log('Sign Out Successfully')
      return state;
      
      case 'SIGNUP_SUCCESS':
         console.log('SignUP successd')
         return {
            ...state ,
            authError : null
         }
      case 'SIGNUP_ERROR' : 
      console.log('SignUp failed')
         return {
            ...state ,
            authError : action.errInfo.message
         }

      default:
         return state
   }
    
}

export default authReducer