import React from 'react'
import { connect } from 'react-redux'
import  { createProject }  from '../../store/actions/projectActions'

import { Redirect } from 'react-router-dom'


class CreateProject extends React.Component{

    constructor(props) {
        super(props)
    
        this.state = {
             title :'',
             content :''
        }
    }

    handleTitle=(event)=>{
        this.setState({
            title:event.target.value
        })
    }

    handleContent=(event)=>{
        this.setState({
            content:event.target.value
        })
    }

    handleSubmit=(event)=>{
        event.preventDefault();
        console.log(this.state)
        this.props.createProject(this.state);
        this.props.history.push('/');
        
    }
    
 
    render(){

        const { auth }=this.props

        if(!auth.uid) return <Redirect to='/SignIn'/>

        return(
            <div>
                <form onSubmit={this.handleSubmit} style={{width:'50%',marginLeft:'25%',marginTop:'10%'}}>
                    <h4>Submit your Idea</h4>
                    <div>
                        <label>Project Title</label>
                        <input type='text' name='title' value={this.state.title} onChange={this.handleTitle}></input>
                    </div>
                    <div>
                        <label>Description</label>
                        <textarea type='text' name='content' value={this.state.content} onChange={this.handleContent}></textarea>
                        {/* <input type='text' name='content' value={this.state.content} onChange={this.handleContent}></input> */}
                    </div>
                    <div>
                        <button className='btn pink'>Create</button>
                    </div>
                </form>
            </div>
        )
    }
}

const mapStateToprops=(state)=>{
      return {
          auth : state.firebase.auth
      }
}

const mapDispatchToProps=(dispatch)=>{
    return {
        createProject : (project)=>dispatch(createProject(project))
    }
}


export default connect(mapStateToprops,mapDispatchToProps)(CreateProject) 