import moment from 'moment'

const ProjectSummary=(props)=>{
    return(
        <div className='card z-depth-0 project-summary' style={{marginTop:'50px'}}>
           <div className='card-content grey-text text-darken-3'>
               <span style={{backgroundColor:'lightgreen',fontSize:'large',fontFamily:'fantasy'}} className='card-title'>{props.title}</span>
                <p>{props.content}</p>
                <p>Owner : {props.author}</p>
                <p className='grey-text'>Posted : {moment(props.date.toDate()).calendar()}</p>
          </div>
        </div>
    )

}

export default ProjectSummary