import React from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
import { signUp } from '../../store/actions/authActions'

class SignUp extends React.Component{

    constructor(props) {
        super(props)
    
        this.state = {
             firstname: '',
             lastname:'',
             email :'',
             password :''
        }
    }

    handleFirstName=(event)=>{
        this.setState({
            firstname:event.target.value
        })
    }

    handleLastName=(event)=>{
        this.setState({
            lastname:event.target.value
        })
    }


    handleEmail=(event)=>{
        this.setState({
            email:event.target.value
        })
    }

    handlePassword=(event)=>{
        this.setState({
            password:event.target.value
        })
    }


    handleSubmit=(event)=>{
         console.log(this.state)
         event.preventDefault();
         this.props.signUp(this.state)
    }
    
    render(){

        const { auth  }=this.props
 
        if(auth.uid) return <Redirect to='/'/>


        return(
            <div>
                <form onSubmit={this.handleSubmit} style={{width:'50%',marginLeft:'25%',marginTop:'10%'}}>
                    <h3>SignUp</h3>
                   <div>
                        <label>First Name</label>
                        <input type='text' name='firstname' required value={this.state.firstname} onChange={this.handleFirstName}></input>
                    </div>
                    <div>
                        <label>Last Name</label>
                        <input type='text' name='lastname' required value={this.state.lastname} onChange={this.handleLastName}></input>
                    </div>
                    <div>
                        <label>Email ID</label>
                        <input type='text' name='email' required  value={this.state.email} onChange={this.handleEmail}></input>
                    </div>
                    <div>
                        <label>Password</label>
                        <input type='password' name='password' required pattern=".{6,}" placeholder='min 6 charecters' value={this.state.password} onChange={this.handlePassword}></input>
                    </div>
                    <div>
                        <button className='btn pink'>SignUp</button>
                    </div>
                </form>
            </div>
        )
    }
}


const mapStateToProps=(state)=>{
    return {
        auth : state.firebase.auth
    }
}

const mapDispatchToProps=(dispatch)=>{
    return {
        signUp : (userInfo)=>dispatch(signUp(userInfo))
    }
}



export default connect(mapStateToProps,mapDispatchToProps)(SignUp) 