import React from 'react'
import { connect } from 'react-redux'
import { signIn } from '../../store/actions/authActions'
import { Redirect } from 'react-router-dom'

class SignIn extends React.Component{

    constructor(props) {
        super(props)
    
        this.state = {
             email :'',
             password :''
        }
    }

    handleEmail=(event)=>{
        this.setState({
            email:event.target.value
        })
    }

     handlePassword=(event)=>{
         this.setState({
             password : event.target.value
         })
     }

    handleSubmit =(event)=>{
        console.log(this.state)
        event.preventDefault()
        this.props.signIn(this.state)
    }
    
 
    render(){

        const { authError , auth  }=this.props
 
        if(auth.uid) return <Redirect to='/'/>

        return(
            <div>
                <form style={{border:'1px solid black'}} onSubmit={this.handleSubmit} style={{width:'50%',marginLeft:'25%',marginTop:'10%'}}>
                   
                    <div>
                        <label>Email ID</label>
                        <input type='email' name='email' required value={this.state.email} onChange={this.handleEmail}></input>
                    </div>
                    <div>
                        <label>Password</label>
                        <input type='password' required name='password' value={this.state.password} onChange={this.handlePassword}></input>
                    </div>
                    <div>
                        <button className='btn pink'>Login</button>
                    </div>
                    <div>
                        {authError ? <p style={{color:'red'}}>{authError}</p> : null}
                    </div>
                </form>
            </div>
        )
    }
}


const mapStateToProps=(state)=>{
    return {
        authError : state.auth.authError,
        auth : state.firebase.auth
    }
}

const mapDispatchToProps=(dispatch)=>{
    return {
        signIn : (creds)=>dispatch(signIn(creds))
    }
}

export default connect(mapStateToProps , mapDispatchToProps )(SignIn)